    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				list:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				isStop:false,
				info:[]
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
			this.loadlist();
		},
         onReachBottom() {
			 this.curpage++;
			 this.loadlist();
         },
        methods: {   
            async loadData() {  
				var _this = this;
				console.log('===userInfo==='+this.hasLogin);
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					_this.$Request.post('member_signin').then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.info =res.datas;
							
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				}
            },
			loadlist() {  
				var _this = this;
				console.log('===userInfo==='+this.hasLogin);
			    if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
			    }else{
					if(!this.hasmore){
						this.loadingText = '暂无更多信息信息';
						return false;
					}
					_this.$Request.post('member_signin/signin_list',{curpage:_this.curpage}).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							console.log(_this.hasmore);
							let _conslist = res.datas.signin_list;
							for(var i=0;i<_conslist.length;i++){
								_this.list.push(_conslist[i]);
							}
							if(!this.hasmore){
								this.loadingText = '暂无更多信息信息';
								return false;
							}
						}
					});
				}
			},
			signin(){
				let _this = this;
				
				_this.$Request.post('member_signin/signin_add').then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.curpage =1;
						_this.list=[];
						_this.hasmore =true;
						_this.loadData();
						_this.loadlist();
						uni.showToast({
							icon: 'none',
							title:'签到成功！',
							duration:2000
						});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
					}
				});
				
			},
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			 
        }  
    }  
